import React from 'react'
import styled from "styled-components";
import Text from './text.component';
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import { Decoration } from './title-decoration.component';

const Card = styled.div`
  box-shadow: 0px 1px 3px 0.5px rgb(0 0 0 / 11%);
  margin: 2.25rem auto;
  max-width: 500px;
 `;

const CardHeader = styled.div`
align-items: center;
  box-sizing: border-box;
  display: flex;
  width: 100%;
  padding: 1rem 1rem 0;
`;

const CardBody = styled.div`
  padding: 1rem;
`;

const DishTitle = styled.div`
  flex: .9;
  text-transform: uppercase;
`;


const DishPrice = styled.div`
  flex: .1;
`;

const DishDescription = styled.div`
  text-align: justify;
  text-transform: uppercase;
  line-height: 1.25em;
`;

const DishImage = styled(GatsbyImage).attrs({
})`
width: 100%;
height: 250px;
`;

export const DishCard = ({ dish = {} }) => {
  const { nombre, precio, descripcion, imagen } = dish;
  const image = getImage(imagen);
  return (
    <>
      <Card>
        <CardHeader>
          <DishTitle>
            <Text variant="highlight">
              {nombre}
            </Text>
          </DishTitle>
          <DishPrice>
            <Text variant="numbers">
              <Decoration>
                {`$${precio}`}
              </Decoration>
            </Text>
          </DishPrice>
        </CardHeader>
        <CardBody>
          <DishDescription>
            <Text variant="body">
              {descripcion}
            </Text>
          </DishDescription>
        </CardBody>
        <DishImage image={image} alt={nombre} />
      </Card>
    </>
  );
}